          >
            <div @click="handelPokerFlop(index)" class="viewport-flip">
              <transition enter-active-class="flipin" leave-active-class="flipout" mode="out-in">
                <img key="bgc" v-if="ispokerBgc[index]" :src="pokerbgc" alt="纸牌背面">
                <img alt="纸牌正面" key="zheng" v-if="!ispokerBgc[index]" :src="item">
